import { DatePipe } from "@angular/common";

export class DateUtil {
  m_days : any[];

  public static is_leap(year) {
    var res;
    return (year%100==0?res=(year%400==0?1:0):res=(year%4==0?1:0));
  }

  public static getDaysInMonth(date) {
    let m_days = new Array(31,28+DateUtil.is_leap(date.getYear()),31,30,31,30,31,31,30,31,30,31);
    let dayTotal = m_days[date.getMonth()];
    let year = date.getFullYear();
    console.log(date);
    let firstday = new Date(DateUtil.addDate(date, -(date.getDate())));
    let tr_str=Math.ceil((dayTotal + firstday.getDay())/7);

    var idx;
    var days = [];
    for(var i=0;i<tr_str;i++) { //表格的行
      for(var k=0;k<7;k++) { //表格每行的单元格
        idx=i*7+k; //单元格自然序列号
        var date_str=idx-firstday.getDay()+1; //计算日期
        (date_str<=0 || date_str>dayTotal) ? date_str=-1 : date_str=idx-firstday.getDay()+1; //过滤无效日期（小于等于零的、大于月总天数的）
        if (date_str == -1) {
          days.push("");
        } else {
          days.push(JSON.stringify(date_str));
        }
      }
    }
    return days;
  }

  public static addDate(date, days){
    var d = new Date(date)
    d.setDate(d.getDate()+days);
    var m=d.getMonth()+1;
    return d.getFullYear()+'-'+m+'-'+d.getDate();
  }

  public static getPreMonth(date) {
    var arr = date.split('-');
    var year = arr[0]; //获取当前日期的年份
    let m_days = new Array(31,28+DateUtil.is_leap(year),31,30,31,30,31,31,30,31,30,31);
    var month = arr[1]; //获取当前日期的月份
    var day = arr[2]; //获取当前日期的日
    var days = new Date(year, month, 0);
    let dayTotal1 = m_days[month-1];
    var year2 = year;
    var month2 = parseInt(month) - 1;
    if (month2 == 0) {
      year2 = parseInt(year2) - 1;
      month2 = 12;
    }
    var day2 = day;
    var days2 = new Date(year2, month2, 0);
    let dayTotal2 = m_days[month2-1];
    if (day2 > days2) {
      day2 = days2;
    }
    var t2 = year2 + '-' + (month2 < 10 ? ('0' + month2) : month2) + '-' + day2;
    return t2;
  }

  /**
   * 获取下一个月
   *
   * @date 格式为yyyy-mm-dd的日期，如：2014-01-25
   */
  public static getNextMonth(date) {
    var arr = date.split('-');
    var year = arr[0]; //获取当前日期的年份
    let m_days = new Array(31,28+DateUtil.is_leap(year),31,30,31,30,31,31,30,31,30,31);
    var month = arr[1]; //获取当前日期的月份
    var day = arr[2]; //获取当前日期的日
    var year2 = year;
    var month2 = parseInt(month) + 1;
    if (month2 == 13) {
      year2 = parseInt(year2) + 1;
      month2 = 1;
    }
    var day2 = day;
    if (day2 > m_days[month2-1]) {
      day2 = m_days[month2-1];
    }
    var t2 = year2 + '-' + (month2 < 10 ? ('0' + month2) : month2) + '-' + day2;
    return t2;
  }

  public static stringToDate(strTime) {
    return new Date(Date.parse(strTime.replace(/-/g,   "/")));
  }

  public static dateToString(date) {
    new DatePipe('en-US').transform(new Date(), 'yyyy-MM-dd');
  }
}



